// ==================================
// googleフォント
// ==================================
@import url('https://fonts.googleapis.com/css2?family=Lato:wght@400;700&family=Noto+Sans+JP:wght@400;500&display=swap');
// ==============================
// 変数
// ==============================

$site-width: 980px;

$shadow-color--s: 0 1px 4px 0 rgba(0, 0, 0, .1);
$shadow-color--m: 0 2px 3px 0 rgba(0, 0, 0, .1);
$shadow-color--l: 0 2px 5px rgba(0, 0, 0, .1);
$shadow-color--xl: 0 2px 10px rgba(0, 0, 0, .1);

$font-color--default: #444444;
$font-color--main: #695B52;
$font-color--accent: #B0673E;
$font-color--link: #847972;
$font-color--hover: #BEB7B3;
$font-color--heading: #695B52;
$font-color--white: #FCFCFC;
$font-color--gray: #CCC;

$bg-color--main: #A09791;
$bg-color--main-light: #BEB7B3;
$bg-color--main-dark: #847972;
$bg-color--base: #F4F4F4;
$bg-color--base-dark: #E1DDD9;
$bg-color--accent: #DEA585;
$bg-color--white: #FCFCFC;
$bg-color--dark: #695B52;
$bg-color--gray: #F4F4F4;

$bg-color--lucent-base: rgba(225, 221, 217, 0.9);
$bg-color--lucent-white: rgba(255, 255, 255, 0.9);

$font-size--s: 12px;
$font-size--m: 14px;
$font-size--default: 16px;
$font-size--l: 20px;
$font-size--xl: 24px;
$font-size--xxl: 32px;
$font-size--xxxl: 40px;

$space--s: 5px;
$space--m: 10px;
$space--l: 15px; 
$space--xl: 20px;
$space--xxl: 30px;
$space--xxxl: 40px;

$font-family--theme: 'Lato', sans-serif;
$font-family--default: 'Lato','Noto Sans JP', 游ゴシック, YuGothic, ヒラギノ角ゴ Pro W3, Meiryo, sans-serif;

// ==================================
// base
// ==================================
html, body {
  // width: 100vw;
  // box-sizing: border-box;
  font-family: $font-family--default;
  font-size: $font-size--default;
  line-height: 1.6;
  letter-spacing: 0.05em;
  text-size-adjust: 100%;
  // color: $font-color--default;
  // background: $bg-color--white;
}

p, a {
  // フォントをスムーズに表示
  -webkit-font-smoothing: antialiased;
}

a {
  color: $font-color--link;
  text-decoration: none;
  transition: .3s;
  &:hover {
    color: $font-color--hover;
    transition: .3s;
  }
}

img {
  width: 100%;
  vertical-align: bottom;
}

ul {
  list-style-type: none;
}

mark {
  background-color:#ff9;
  color:#000;
  font-style:normal;
  font-weight:bold;
}
